/* For Button */
.cr-btn-default {
    display: inline-block;
    height: 60px;
    line-height: 62px;
    padding: 0px 50px;
    font-size: 14px;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 1px;
    border-radius: 50px;
    font-weight: 700;
    color: #00b4ff;
    font-family: Circular Std Bold;
    position: relative;
    z-index: 1;
    cursor: pointer;
    transition: all 0.3s ease;
}

.svg-btn-effect {
    position: relative;
    display: inline-block;
}
.svg-btn-effect svg {
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
}
.svg-btn-effect svg rect {
    fill: none;
    stroke: #00b4ff;
    stroke-dasharray: 353,0;
    stroke-width: 2px;
    stroke-dashoffset: 0;
    transition: all 1s ease;
}
.svg-btn-effect:hover svg rect {
    stroke-dasharray: 154,537;
    stroke-dashoffset: 420;
}
.cr-btn-default{
	color:#00b4ff !important;
	text-decoration: none !important;
}
.cr-btn-default:hover{
	color:#00b4ff !important;
}
